    postTicket()
    {
        var bodyFormData = new FormData();
        bodyFormData.set( "name", "k" );
        bodyFormData.set( "from", "ka@ka.c" );
        bodyFormData.set( "type", "1" );
        bodyFormData.set( "vessel", "axiosPost" );
        bodyFormData.set( "engine", "axiosPost" );
        bodyFormData.set( "subject", "axiosPost");
        bodyFormData.set( "reply", "axiosPost" );

        axios({
            method: 'post',
            url: API_URL_DOKIMASTIKO_GIA_TICKET+'/en/customer/create-ticket/',
            data: bodyFormData,
            config: { headers: {'Content-Type': 'multipart/form-data' }}
        })
            .then(function (response) {
                //handle success
                console.log(response);
            })
    }

